import React, { useState, useContext, useReducer, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import { Container, Col, Row, Form, Button } from "react-bootstrap";

import * as Api from "../../utils/api";
import { DispatchContext, UserStateContext } from "../../../App";
import {
  emailValidateReducer,
  paswordValidateReducer,
} from "../../hooks/validatorReducer";

function LoginForm() {
  const navigate = useNavigate();
  const dispatch = useContext(DispatchContext);
  const [emailState, dispatchEmail] = useReducer(emailValidateReducer, {
    value: "",
    isEmailValid: null,
  });
  const [passwordState, dispatchPassword] = useReducer(paswordValidateReducer, {
    value: "",
    isPasswordValid: null,
  });
  const [isFormValid, setIsFormValid] = useState(false);
  const userState = useContext(UserStateContext);

  // //      .
  const { isEmailValid, value: emailValue } = emailState;
  const { isPasswordValid, value: passwordValue } = passwordState;

  useEffect(() => {
    setIsFormValid(isEmailValid && isPasswordValid);
  }, [isEmailValid, isPasswordValid]);

  useEffect(() => {
    if (!userState.user) {
      navigate("/login", { replace: false });
      return;
    } else if (userState.user) {
      navigate("/", { replace: true });
    }
  }, []);

  const handleSubmit = async (e) => {
    e.preventDefault();

    try {
      // "user/login"  post.
      const res = await Api.post("user/login", {
        email: emailValue,
        password: passwordValue,
      });
      console.log("", res);
      //   response data.
      const user = res.data;
      // dispatch      .
      dispatch({
        type: "LOGIN_SUCCESS",
        payload: user,
      });

      //   .
      // window.location.href = "/";
      navigate("/", { replace: true });
    } catch (err) {
      console.log(" .\n", err);
    }
  };

  return (
    <Container>
      <Row className="justify-content-md-center mt-5">
        <Col lg={8}>
          <Form onSubmit={handleSubmit}>
            <Form.Group controlId="loginEmail">
              <Form.Label> </Form.Label>
              <Form.Control
                type="email"
                autoComplete="on"
                value={emailState.value}
                onChange={(e) =>
                  dispatchEmail({
                    type: "EMAIL_VALIDATOR",
                    value: e.target.value || "",
                  })
                }
                onBlur={() => dispatchEmail({ type: "INPUT_BLUR" })}
              />
              {!isEmailValid && (
                <Form.Text className="text-success">
                     .
                </Form.Text>
              )}
            </Form.Group>

            <Form.Group controlId="loginPassword" className="mt-3">
              <Form.Label></Form.Label>
              <Form.Control
                type="password"
                autoComplete="on"
                value={passwordState.value}
                onChange={(e) =>
                  dispatchPassword({
                    type: "PASSWORD_VALIDATOR",
                    value: e.target.value || "",
                  })
                }
                onBlur={() => dispatchPassword({ type: "INPUT_BLUR" })}
              />
              {!isPasswordValid && (
                <Form.Text className="text-success">
                   5 .
                </Form.Text>
              )}
            </Form.Group>

            <Form.Group as={Row} className="mt-3 text-center">
              <Col sm={{ span: 20 }}>
                <Button variant="primary" type="submit" disabled={!isFormValid}>
                  
                </Button>
              </Col>
            </Form.Group>

            <Form.Group as={Row} className="mt-3 text-center">
              <Col sm={{ span: 20 }}>
                <Button variant="light" onClick={() => navigate("/register")}>
                  
                </Button>
              </Col>
            </Form.Group>
          </Form>
        </Col>
      </Row>
    </Container>
  );
}

export default LoginForm;
